import React from 'react'
import { translate } from 'react-i18next'
import MarkdownPage from '../components/MarkdownPage'

const source = `
### Harjutus 1.
         ( 4)    :


### Harjutus 2.
  -.       :
1. Helistage kas .......... (enne, jooksul, mber) vi ......... (saadik, prast, alates) lunat.
2. ra minu .......... (peale, jrgi, prast) muretse!
3. Selle professori loengud algavad .......... (suhtes, alates, alla) mrtsist.
4. .......... (Koos, Ilma, Teel) heade pikuteta on raske ppida.
5. Ma ostan need vihikud lapse .......... (jaoks, kaupa, res).
6. Toas on vett .......... (kaudu, alla, kuni) plveni.
7. pilane puudub homme vistluste ..........(sees, tttu, alates).
8. .......... (Peale, le, Enne) tema ei tea sellest keegi.
9. Kelle .......... (mda, kes, teel) on raamat?
10. Vanemad teevad kik oma lapse .......... (kestel, eel, heaks).
`

export default translate()(props => <MarkdownPage title={props.t('task', { number: 11 })} source={source}/>)



