import React from 'react'
import { translate } from 'react-i18next'
import MarkdownPage from '../components/MarkdownPage'

const source = `
### Harjutus 1.
   .
Vikesed lapsed armastavad eriti televiisorit (vaatama, vaadata). 2. Tulge minuga kohvi (jooma, juua). 3. Ema hakkab lauda (katma, katta), aga tal on vaja veel tna pesu (pesema, pesta). 4. Minu kht on thi, ma tahan (sma, sa). 5. Me lheme kell 10 (magama, magada). 6. lipilased peavad palju raamatukogus (istuma, istuda) ning raamatuid (lugema, lugeda). 7. Halb on (elama, elada) ksi. 8. Kas sa saad mulle (laenama, laenata) 100 eurot. 9. Meil on tarvis poes (kima, kia). 10. Praegu ma lhen (ujuma, ujuda), ja prast ma jn kaldale (pevitama, pevitada). 11. Alates sgisest asun ma Tartu likooli (ppima, ppida). 12. Vlismaalastel pole kerge eesti keelt selgeks (saama, saada). 13. Vabandust, ma tiesti unustasin seda (tegema, teha). 14. Kuhu sidab Peeter suvel (puhkama, puhata)? 15. Anna ei oska veel hsti (rkima, rkida), aga ta tahab juba koolis (ppima, ppida).

### Harjutus 2.
   ma-: -ma, -mas, -mast,- maks, -mata.
Minu ttar kib .......... (iluuisutama). 2. Ma ei taha htegi peva .......... (ppima) jtta. 3. Mis kell me .......... (jalutama) tagasi tuleme? 4. Kik lhevad baari htul .......... (tantsima) ja .......... (laulma). 5. Teiega pole vimalik ilma .......... (vaidlema) vestelda. 6. Poeg on praegu hambaid .......... (pesema), ta ei lhe magama hambaid .......... (pesema). 7. Kes on juba .......... (pildistama) kinud? 8.Aith, aga ma tulen juba .......... (sma). 9. .......... (Nitama) oma nusolekut, tstavad kik ked. 10. Talvel vib minna lumesda .......... (mngima) ja lumememme .......... (tegema).
`

export default translate()(props => <MarkdownPage title={props.t('task', { number: 14 })} source={source}/>)



