import React from 'react'
import { translate } from 'react-i18next'
import MarkdownPage from '../components/MarkdownPage'

const source = `
### Harjutus 1.
     .
1. On kasulik suhelda aruka inimesega. 2. Ta alati ostab poest kollaseid banaane ja rohelisi pirne. 3. See on sna asjalik ettekanne. 4. Kinkime noorele spetsialistile viis pikka punast tulpi. 5. Mina olen suurelt kaardilt leidnud kuumalise nimega linna. 6. Tallinnas on suured ja pikad tnavad. 7. Rikka lipilase uhke auto seisab likooli ees. 8. Tal on heledad sirged juuksed, pikergune ngu ja suured sinised silmad. 9. Vaata kui naljakas mees seisab meie taga, tal on nii imelik habe! 10. Taavi on khn, aga tal on sportlik figuur. 

### Harjutus 2.
   .   .
1.Minu vend on ......... pilane. 2. Tna on .......... ilm. 3. Mulle meeldivad .......... inimesed. 4. Eesti on .......... riik, ja eestlased on .......... rahvas. 5. See t on vga .......... . 6. Mgedes on alati nii .......... hk. 7. Minu kass on .......... . 8. Naisel on seljas .......... pluus. 9. Sgisel on puudel .......... , .......... ja .......... lehed. 10. Mina olen .......... inimene, kuna mul on palju spru.

### Harjutus 3.
      .
1. Triinu on .......... (keskmise) kasvu .......... (noor) naine. 2. Palun osta mulle .......... (Vene juust) ja .......... (Eesti piim)! 3. Tore on kohata .......... (meeldiv) inimest. 4. Kirjutan kuupeva .......... (Araabia) numbritega. 5. Mina pin koolis .......... (prantsuse keel), aga soovin veel ppida .......... (saksa keel). 6.Nad on .......... (pikk) tpevast vsinud. 7. Ajakirjanik unistab .......... (uus) raamatust. 8. Leningradi loomaaias vib nha ......... (amuuri tiiger) ja .......... (siberi karu). 9. Mees ostab he .......... (punane) roosi oma ......... (kallis) naisele. 10. Ma tunnen hsti seda .......... (khnavitu tdruk).
`

export default translate()(props => <MarkdownPage title={props.t('task', { number: 12 })} source={source}/>)



